import '../loading'
import {default as util} from 'util'

const tpl = require('./index.html')
const dom = Regular.dom
const body = document.querySelector('.g-doc') || document.body
let counter = 0

/**
 * data
 * - size
 *   - lg/large
 *   - sm/small
 * - autoShow 
 * - autoDestroy 
 * - autoDestroyWhenCancel 
 * - autoHideWhenConfirm 
 * - autoDestroyWhenConfirm 
 * - cancelTitle 
 * - confirmTitle 
 * cancel/confirm  hide ,  afterFadeOut
 */
export default Regular.extend({
  name: 'modal',
  template: tpl,
  config () {
    this.initDefault()
    this.$on('afterFadeOut', this.afterFadeOut.bind(this))
  },
  initDefault () {
    util.fillUndef(this.data, {
      autoShow: true,
      autoDestroy: true,
      autoDestroyWhenCancel: true,
      autoHideWhenConfirm: true,
      autoDestroyWhenConfirm: true,
      cancelTitle: ' ',
      confirmTitle: ' '
    })
  },
  computed: {
    sizeClazz () {
      switch (this.data.size) {
        case 'lg':
        case 'large':
          return 'modal-lg'
        case 'sm':
        case 'small':
          return 'modal-sm'
        default:
          break
      }
      return ''
    }
  },
  init () {
    this.hideDom()
    if (this.data.autoShow) {
      this.show()
    }
  },
  hideDom () {
    this.$refs.backdrop.style.display = 'none'
    this.$refs.modal.style.display = 'none'
  },
  showDom () {
    this.$refs.backdrop.style.display = 'block'
    this.$refs.modal.style.display = 'block'
  },
  show () {
    counter++
    dom.addClass(body, 'modal-open')
    this.showDom()
    this.$inject(body)
    setTimeout(() => {
      this.data.show = true
      this.$update()
    }, 0)
  },
  click (event) {
    // console.log(event)
    if (event.target === this.$refs.modal) {
      this.cancel()
    }
  },
  /**
   * cancel & confirm ,  UI ,  afterFadeOut 
   *  cancel & confirm,  this.supr()  reason,  afterFadeOut 
   */
  cancel () {
    this.hide()
    this.data.reason = 'cancel'
    this.$emit('cancel')
  },
  act (action) {
    this.confirm(action)
  },
  confirm (action) {
    if (this.data.autoHideWhenConfirm) {
      this.hide()
    }
    this.data.reason = 'confirm'
    let eventName = 'confirm'
    if (action) {
      eventName = action.value
    }
    this.$emit(eventName, action)
  },
  hide () {
    this.data.show = false
    this.data.hide = true
    this.$update()
  },
  afterFadeOut () {
    //  Modal,  class
    counter--
    if (counter === 0) {
      dom.delClass(body, 'modal-open')
    }

    this.data.hide = false
    this.data.show = false
    this.hideDom()
    const shouldDestroy = (this.data.reason === 'cancel' &&
      this.data.autoDestroyWhenCancel &&
      this.data.autoDestroy) ||
      (this.data.reason === 'confirm' &&
      this.data.autoDestroyWhenConfirm &&
      this.data.autoDestroy)
    if (shouldDestroy) {
      this.destroy()
    }
  }
})



